/* 1. Global - Set up the things
/* ---------------------------------------------------------- */

@import "global.css";
body {
    background-color: #1a1b21;
    background-image: url(/assets/img/bg.png);
    background-attachment: fixed;
    overflow-x: unset;
    color: #ccc;
}

.img {
    display: block;
    width: 100%;
    height: 100%;
    background-position: center center;
    background-size: cover;
    border-radius: 100%;
}

.hidden {
    visibility: hidden;
    position: absolute;
    text-indent: -9999px;
}

@media (min-width: 680px) {
    .lg-hidden {
        display: none;
    }
}

@media (max-width: 679px) {
    .hidden-md {
        display: none;
    }
}

.display-none {
    display: none;
}


/* 2. Layout - Page building blocks
/* ---------------------------------------------------------- */

.site-wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.site-main {
    z-index: 100;
    flex-grow: 1;
}


/* Full width page blocks */

.outer {
    position: relative;
    padding: 0 4vw;
}

@media (max-width: 500px) {
    .outer {
        padding: 0 1rem;
    }
}


/* Centered content container blocks */

.inner {
    margin: 0 auto;
    max-width: 1040px;
    width: 100%;
}


/* Usage:

<div class="outer">
    <div class="inner">
        Centered content
    </div>
</div>

*/


/* 3. Special Template Styles
/* ---------------------------------------------------------- */


/* All Pages */

.page-content {
    background: #1a1d2f;
    box-shadow: inset #232940 0px 0px 9px 4px;
    border-radius: 5px;
}

.page-content p {
    line-height: 1.5;
}


/* About Page */

#aboutContainer {
    padding-bottom: 1rem;
}

#links>.social-media-links {
    display: flex;
    margin: 0 5rem 1rem;
    padding-bottom: 2rem;
    justify-content: space-between;
    font-size: 4rem;
}

#links>.social-media-links a {
    box-shadow: none;
}

#links>.social-media-links a i {
    text-shadow: #000 1px 1px 6px;
    text-decoration: none;
}

#links>.social-media-links a:hover {
    text-decoration: none;
}

#links>.social-media-links .twitter:hover,
#links>.social-media-links .twitter:active,
#links>.social-media-links .twitter:focus {
    color: #1da1f2;
}

#links>.social-media-links .discord:hover,
#links>.social-media-links .discord:active,
#links>.social-media-links .discord:focus {
    color: #7289da;
}

#links>.social-media-links .twitch:hover,
#links>.social-media-links .twitch:active,
#links>.social-media-links .twitch:focus {
    color: #6441a4;
}

#links>.social-media-links .youtube:hover,
#links>.social-media-links .youtube:active,
#links>.social-media-links .youtube:focus {
    color: #ff0000;
}

#links>.social-media-links .steam:hover,
#links>.social-media-links .steam:active,
#links>.social-media-links .steam:focus {
    color: #000;
}

#links>.social-media-links a:active {
    text-shadow: none;
    left: 1px;
    top: 1px;
    position: relative;
}


/** Custom Pages **/


/** About page centering */

.page-template.page-about .site-main {
    display: flex;
    align-items: center;
    padding: 0;
}

.page-description {
    padding: 2rem;
    font-family: Open Sans;
    font-size: 2.5rem;
    line-height: 2em;
    color: #ccc;
}

.page-content p:last-child {
    margin: 0;
}

.page-footer {
    display: flex;
    justify-content: center;
    align-content: center;
    margin: 1rem 0;
}

.icon-footer {
    height: 100px;
    width: 100px;
    background-image: url(/assets/img/icon-eighthres.png);
    background-size: cover;
    opacity: 0.8;
}


/** Creators Page **/

.icons-container {
    display: flex;
    justify-content: space-between;
    position: relative;
    flex-wrap: wrap;
}

.user-figure {
    text-align: center;
    padding: 1rem;
    flex-shrink: 1;
    flex-grow: 1;
    min-width: 15%;
    max-width: 100%;
}

@media (max-width: 679px) {
    .user-figure {
        padding: 1.25rem 0 1.25rem;
        width: 50%;
    }
}

.user-caption {
    font-size: 3.5rem;
    line-height: 1;
    color: #ccc;
    font-family: Open Sans Condensed;
    text-shadow: 2px 2px #000;
    word-wrap: break-word;
}

@media(min-width:680px) {
    .profile-icon {
        opacity: 0.4;
        transition: opacity 0.25s;
    }
    .profile-icon:hover {
        opacity: 1;
    }
}

.profile-icon {
    max-width: 100px;
    max-height: 100px;
}

#profiles-container {
    position: fixed;
    z-index: 100;
    left: 5%;
    width: 90%;
}

@media (min-width: 1190px) and (max-width: 1499px) {
    #profiles-container {
        left: 15%;
        width: 70%;
    }
}

@media (min-width: 1500px) and (max-width: 1899px) {
    #profiles-container {
        left: 25%;
        width: 50%;
    }
}

@media (min-width: 1900px) {
    #profiles-container {
        left: 30%;
        width: 40%;
    }
}

.creator-name {
    line-height: 1;
}

.creator-profile {
    padding: 1rem;
}

@media(max-height: 799px) {
    .profiles-container-bottom {
        bottom: 1rem;
    }
    .profiles-container-top {
        top: 5rem;
    }
}

@media(max-height: 730px) {
    #profiles-container {
        display: none;
    }
}

.creator-profile {
    display: none;
    color: #ddd;
}

.creator-name {
    font-size: 7rem;
    font-family: Open Sans Condensed;
    padding-left: 1.8rem;
    padding-right: 2rem;
    margin-left: 2.25rem;
    border-radius: 5px;
    border: 2px ridge #a3a3a3;
    display: inline-flex;
    line-height: 1.2;
    padding-bottom: 1.25rem;
    margin-bottom: -1rem;
}

.creator-bio {
    font-size: 2.5rem;
    text-align: center;
    padding: 1rem;
    line-height: 1.5;
}

.rpg-box {
    background: linear-gradient(180deg, #222942 0, #1a1d2f);
    border: 2px ridge #a3a3a3;
    border-radius: 5px;
    box-shadow: 5px 5px 10px 0 #080812;
}

.rpg-box.text-emphasis {
    text-shadow: 2px 2px #000;
}


/**
* Browse posts/search page
*/

#searchForm {
    width: 66%;
    margin: 1rem auto;
    height: 5rem;
    display: flex;
    justify-content: center;
    padding-bottom: 0;
}

#searchField {
    height: 3.5rem;
    width: 100%;
    background: transparent;
    border: none;
    font-size: 2rem;
}

@media (max-width: 869px) {
    #searchForm {
        width: 95%;
    }
}

@media (max-width: 520px) {
    #searchField {
        font-size: 1.5rem;
    }
}

.search-container>.icon-search {
    font-size: 2rem;
    color: #bfcde0;
    padding: 0 1rem 0 0.5rem;
}

.search-container {
    position: relative;
    width: 95%;
    display: flex;
    align-items: center;
}

.results-header {
    font-size: 2.5rem;
    margin: 1rem auto 2rem;
    width: 100%;
    text-align: center;
}


/* 4. Site Header
/* ---------------------------------------------------------- */

.site-header {
    height: 90px;
    position: fixed;
    width: 100%;
    color: #fff;
    padding-top: 2px;
    z-index: 300;
}

.site-header-border {
    border-image: url(/assets/img/tooth.png);
    border-image-outset: 15px;
    border-image-slice: 0 1 19;
    border-image-repeat: repeat;
    border-width: 0 0 16px;
    border-style: solid;
    position: absolute;
    height: 90px;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    opacity: 1;
}

.site-header.no-cover:before,
.site-header.no-cover:after {
    display: none;
}

.site-header-content {
    text-align: center;
}

.site-title {
    z-index: 10;
    margin: 0;
    padding: 0;
    font-size: 3.8rem;
    font-weight: 700;
}

.site-logo {
    max-height: 45px;
}

.site-description {
    z-index: 10;
    margin: 0;
    padding: 5px 0;
    font-size: 2.2rem;
    font-weight: 300;
    letter-spacing: 0.5px;
    opacity: 0.8;
}

@media (max-width: 500px) {
    .site-title {
        font-size: 3rem;
    }
    .site-description {
        font-size: 1.8rem;
    }
}


/***
/* Custom Logo
/****/

div.scaleTitle {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: left;
    transform-origin: top left;
    float: left;
    position: relative;
    z-index: 200;
}


/* For mobile and narrow screens, keep logo size static. */

@media (max-width: 869px) {
    div.mainTitle {
        display: flex;
    }
    .scaleTitle a {
        text-decoration: none;
    }
}

@media (max-width: 869px) and (min-width: 680px) {
    .scaleTitle a {
        transform: scale(0.75);
    }
    div.scaleTitle {
        width: 520px;
    }
}

@media (max-width: 679px) {
    div.scaleTitle {
        transform-origin: top center;
        float: none;
    }
}

@media (max-width: 520px) {
    div.scaleTitle {
        transform-origin: top left;
        float: left;
        margin-left: 5%;
    }
}

@media (max-width: 460px) {
    .scaleTitle a {
        transform: scale(0.75);
    }
}

@media (max-width: 380px) {
    .scaleTitle a {
        transform-origin: left center;
    }
    div.scaleTitle {
        margin-left: 2%;
    }
}


/* 5. Site Navigation
/* ---------------------------------------------------------- */

.site-nav {
    width: 100%;
    position: relative;
    z-index: 100;
    display: flex;
    justify-content: center;
    justify-items: center;
    align-items: center;
}

.site-nav-left {
    display: flex;
    align-items: center;
    overflow-x: auto;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    margin-right: 10px;
    padding-bottom: 80px;
    letter-spacing: 0.4px;
    white-space: nowrap;
    -ms-overflow-scrolling: touch;
}


/* Site Nav Hack Explanation (above):

What's happening above it .site-nav-left is set to overflow-x and allow sideways scrolling, so that when there isn't enough space for all nav items (either due to lots of nav items, or a small viewport), you can still scroll side-to-side to reach them.

The knock-on effect of this is ugly browser-scroll bars at the bottom, so 80px of padding-bottom and a 40px fixed height parent (.site-nav) hides that entirely. Slightly hacky code. But nice clean end-result.

*/

.site-nav-logo {
    flex-shrink: 0;
    display: block;
    margin-right: 24px;
    padding: 11px 0;
    color: #fff;
    font-size: 1.7rem;
    line-height: 1em;
    font-weight: bold;
    letter-spacing: -0.5px;
}

.site-nav-logo:hover {
    text-decoration: none;
}

.site-nav-logo img {
    display: block;
    width: auto;
    height: 21px;
}

.nav {
    display: flex;
    margin: 0 0 0 -12px;
    padding: 0;
    list-style: none;
}

.nav li {
    display: block;
    margin: 0;
    padding: 0;
    text-transform: uppercase;
}

.nav li a {
    display: block;
    margin: 0;
    padding: 10px 12px;
    color: #fff;
    opacity: 0.8;
}

.nav li a:hover {
    text-decoration: none;
    opacity: 1;
}

.site-nav-right {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    height: 40px;
}

.social-links {
    flex-shrink: 0;
    display: flex;
    align-items: center;
}

.social-links a:last-of-type {
    padding-right: 20px;
}

.social-link {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 10px;
    color: #fff;
    opacity: 0.8;
}

.social-link:hover {
    opacity: 1;
}

.social-link svg {
    height: 1.8rem;
    fill: #fff;
}

.social-link-fb svg {
    height: 1.5rem;
}

.social-link-wb svg {
    height: 1.6rem;
}

.social-link-wb svg path {
    stroke: #fff;
}

.social-link-rss svg {
    height: 1.9rem;
}

.subscribe-button {
    display: block;
    padding: 4px 10px;
    border: #fff 1px solid;
    color: #fff;
    font-size: 1.2rem;
    line-height: 1em;
    border-radius: 10px;
    opacity: 0.8;
}

.subscribe-button:hover {
    text-decoration: none;
    opacity: 1;
}

.rss-button {
    opacity: 0.8;
}

.rss-button:hover {
    opacity: 1;
}

.rss-button svg {
    margin-bottom: 1px;
    height: 2.1rem;
    fill: #fff;
}

@media (max-width: 679px) {
    .site-header {
        padding-right: 0;
        padding-left: 0;
    }
    .site-nav-left {
        margin-right: 0;
        padding-left: 4vw;
    }
    .site-nav-right {
        display: none;
    }
}


/** Custom Navbar (WIP) */

.site-nav ul {
    list-style: none;
}

.site-nav ul>li {
    font-size: 3em;
    display: inline;
    margin: 0 1em;
    padding: 5px;
    line-height: normal;
    text-shadow: 2px 2px black;
    font-family: 'Open Sans Condensed';
    cursor: default;
}

.site-nav a {
    color: #ddd;
    text-decoration: none;
}

.site-nav a:hover {
    text-shadow: 0px 0px 5px #fff;
}


/* Divider between navbar menu items. */

.site-nav ul>li:after {
    content: '\2502';
    transform: scaleX(0.8);
    position: absolute;
    color: #777;
    margin-left: 30px;
}

.site-nav ul>li:last-child:after {
    content: none;
}


/*
* For the smartass desktop users that just HAVE to see what the site looks 
* like at a narrow window size, we have to be a little creative because Skrollr 
* will be loaded and overrides any attempts to scale the logo and change other 
* scrolling-dependent elements.
*/

@media(max-width: 679px) {
    .scaleTitle.skrollable {
        left: 0% !important;
        transform: scale(0.5) translateX(0%) !important;
    }
    .site-header.skrollable {
        background: rgba(21, 24, 40, 1) !important;
    }
    .site-main.skrollable {
        margin-top: 110px !important;
    }
}

@media(max-width: 520px) {
    .scaleTitle.skrollable {
        margin-left: 3%;
    }
}

@media (max-width: 380px) {
    .burger {
        right: 25px;
    }
}


/*
* Skrollr will override these rules. These are included
* to handle users with JavaScript disabled.
*/

.scaleTitle {
    transform: scale(0.5);
}

.site-nav {
    position: absolute;
    top: 9px;
    left: 120px;
    opacity: 1;
    transform: scale(0.6) translateX(10%);
}

@media (max-width: 869px) {
    .site-nav {
        left: 90px;
    }
}

.site-header {
    background: rgba(21, 24, 40, 1);
}

.site-main {
    margin-top: 110px;
}


/** Manually adjust the nav bar as needed for narrow resolutions. */


/** Lessen the margins and position the navbar relatively. */

@media (max-width: 1599px) {
    .site-nav ul>li {
        margin: 0 0.5em;
    }
    .site-nav ul>li:after {
        margin-left: 8px;
    }
    ul#navbar {
        position: relative;
    }
}


/** Shrink logo and navbar. */

@media (max-width: 869px) {
    .site-nav ul {
        top: 0.5em;
    }
    .site-nav ul>li {
        font-size: 2.5em;
    }
}


/**
* Hamburger menu.
**/

.burger,
.burger-check {
    display: none;
}

@media (max-width: 679px) {
    .burger-check {
        display: inline-block;
    }
    .burger {
        color: #ddd;
        display: inline-block;
        border: 0;
        background: none;
        outline: 0;
        padding: 0;
        cursor: pointer;
        border-bottom: 4px solid currentColor;
        width: 28px;
        position: absolute;
        right: 35px;
        top: 35px;
        z-index: 1000;
    }
    .burger::-moz-focus-inner {
        border: 0;
        padding: 0;
    }
    .burger:before {
        content: "";
        display: block;
        border-bottom: 4px solid currentColor;
        width: 100%;
        margin-bottom: 5px;
        transition: transform 0.5s ease-in-out;
    }
    .burger:after {
        content: "";
        display: block;
        border-bottom: 4px solid currentColor;
        width: 100%;
        margin-bottom: 5px;
        transition: transform 0.5s ease-in-out;
    }
    .burger-check {
        display: none;
    }
    .site-nav {
        overflow: hidden;
        max-height: 0;
        transition: max-height 0.5s ease-in-out;
        transform: none;
        left: 0px;
        top: 90px;
    }
    .site-nav ul {
        top: 0;
        width: 100%;
        padding: 0;
    }
    .site-nav ul>li {
        display: block;
        font-size: 2em;
        padding: 0;
        margin: 0;
        margin-top: -1px;
    }
    .site-nav ul>li:after {
        margin-left: 0;
        content: "";
        transform: none;
    }
    /** 
    * web dev gods forgive me for i am about to sin. for what it's 
    * worth these importants will only override in a wonky edge case
    * (load page in large browser and manually shrink it).
    */
    .site-nav.skrollable {
        transform: none !important;
        top: 90px !important;
        left: 0px !important;
        opacity: 1 !important;
    }
    .skrollable#navbar {
        left: 0px !important;
    }
    .site-nav ul>li:nth-child(odd) {
        background: #222840;
    }
    .site-nav ul>li:nth-child(even) {
        background: #1e1e30;
    }
    .burger-check:checked~.site-nav {
        max-height: 500px;
        transition: max-height 0.5s ease-in-out;
    }
}


/* 6. Post Feed
/* ---------------------------------------------------------- */

.post-feed {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px;
}

.post-card {
    flex: 1 1 300px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin: 0 20px 40px;
    min-height: 300px;
    background-size: cover;
    border-radius: 5px;
    box-shadow: 5px 4px 10px 0px black;
}

.post-card-image-link {
    position: relative;
    display: block;
    overflow: hidden;
    border-radius: 5px 5px 0 0;
}

.post-card-image {
    width: auto;
    height: 200px;
    background: var(--lightgrey) no-repeat center center;
    background-size: cover;
}

.post-card-content-link {
    position: relative;
    flex-grow: 1;
    display: block;
    padding: 25px 25px 0;
    color: var(--bright-text);
}

.post-card-content-link:hover {
    text-decoration: none;
}

.post-card-tags {
    display: block;
    margin-bottom: 4px;
    color: var(--midgrey);
    font-size: 1.2rem;
    line-height: 1.15em;
    font-weight: 500;
    letter-spacing: 0.5px;
    text-transform: uppercase;
}

.post-card-title {
    margin-top: 0;
}

.post-card-content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.post-card-excerpt {
    font-family: 'Open Sans';
}

.post-card-meta {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding: 0 25px 25px;
}

.author-profile-image {
    margin-right: 5px;
    width: 40px;
    height: 40px;
    border-radius: 100%;
    object-fit: cover;
}

.post-card-meta .profile-image-wrapper,
.post-card-meta .avatar-wrapper {
    position: relative;
}

.post-card-mini .post-card-title {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    line-height: 1.5;
    margin-bottom: 0;
}

.post-card-mini {
    display: inline-flex;
    width: calc(50% - 9px);
    height: 85px;
    margin: 5px 5px 5px 0;
}

.post-card-mini .post-card-content {
    min-width: 0;
}

.post-card-mini:nth-child(2n+1) {
    margin: 5px 0 5px 5px;
}

.post-card-mini .post-card-image {
    height: 100%;
    width: 100%;
}

.post-card-mini a.post-card-image-link {
    min-width: 140px;
    border-radius: 5px 0 0 5px;
    height: auto;
    display: inline-block;
}

.post-card-mini .post-card-content-link {
    display: inline-block;
    padding: 0.75rem 0 0 2rem;
}

.post-card-mini .post-card-meta {
    padding: 0 0 1rem 2rem;
}

.post-card-mini .author-profile-image {
    margin: 0;
    width: 32px;
    height: 32px;
}

.post-card-mini .author-list {
    margin-right: 1rem;
    margin-top: 1rem;
}

.post-card-mini .static-avatar {
    width: 32px;
    height: 32px;
}

.post-card-mini .post-card-meta {
    align-items: center;
}

@media(max-width:869px) {
    .post-card-mini {
        flex-direction: column;
        overflow: hidden;
        min-height: 220px;
        width: calc(33% - 9px);
        margin: 5px;
    }
    .post-card-mini:nth-child(2n+1) {
        margin: 5px;
    }
    .post-card-mini:nth-child(3n+1) {
        margin: 5px 0 5px 5px;
    }
    .post-card-mini a.post-card-image-link {
        display: block;
        position: relative;
    }
    .post-card-mini .post-card-image {
        display: block;
        height: 92px;
        width: auto;
    }
    .post-card-title {
        height: 5.75rem;
        font-size: 1.75rem;
    }
    .post-card-mini .post-card-title {
        white-space: normal;
    }
    .post-card-mini .post-card-content-link {
        padding: 0.75rem 1rem 0 1rem;
    }
}

@media (max-width: 767px) {
    .post-card-mini {
        width: calc(50% - 10px);
        margin: 5px;
    }
    .post-card-mini:nth-child(2n+1) {
