    padding-bottom: 56.25%;
}

@media (max-width: 1607px) {
    #vidContainer {
        width: 100%;
        height: 100%;
        margin-left: 0;
    }
}

.post-full {
    position: relative;
    z-index: 50;
}


/* ^ Required to make .post-full-content:before/after z-index stacking work */

.post-full-header {
    margin: 0 auto;
    padding: 0 3vw 1rem;
    max-width: 1040px;
    text-align: center;
}

.post-full-meta {
    display: flex;
    justify-content: center;
    align-items: center;
